import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ClarityModule } from 'clarity-angular';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { NewComponent } from './new.component';
import { NewRoutes } from './new.routes';
import { RouterModule } from '@angular/router';
import { NewSightComponent } from './sight/sight.component';
import { NewBusinessComponent } from './business/business.component';
import { NewOrganizationComponent } from './organization/organization.component';
import { NewPostComponent } from './post/post.component';
import { SharedModule } from '../shared/shared.module';
import { AgmCoreModule } from '@agm/core';
import { Ng2ImgToolsModule } from 'ng2-img-tools';
import { ImageUploadModule } from 'ng2-imageupload';
import { TextMaskModule } from 'angular2-text-mask';

@NgModule({
  imports: [
    CommonModule, 
    RouterModule.forChild(NewRoutes), 
    ClarityModule.forRoot(), 
    FormsModule, 
    ReactiveFormsModule, 
    SharedModule.forRoot(),
    AgmCoreModule,
    ImageUploadModule,
    Ng2ImgToolsModule,
    TextMaskModule
  ],
  declarations: [
  	NewComponent,
  	NewSightComponent,
    NewBusinessComponent,
    NewOrganizationComponent,
    NewPostComponent
  ]
})
export class NewModule { }


