import { Component, ElementRef, NgModule, NgZone, OnInit, ViewChild } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState, UserState, ViewState } from '../../store/state';
import { AppActions } from '../../store/actions/appActions';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { ImageResult, ResizeOptions } from 'ng2-imageupload';
import { AgmCoreModule, MapsAPILoader } from '@agm/core';
import { AgmMap } from '@agm/core';
import * as firebase from 'firebase';
import { AngularFireDatabase } from 'angularfire2/database';
import { generateUUID, getCity } from '../../shared/functions/functions';

@Component({
  selector: 'app-newpost',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.scss']
})
export class NewPostComponent implements OnInit {

  model: FormGroup = new FormGroup({
    name: new FormControl('', Validators.required),
    type: new FormControl('Event', Validators.required),
    start_date: new FormControl('', Validators.required),
    end_date: new FormControl('', Validators.required),
    website: new FormControl(''),
    description: new FormControl(''),
    isFeatured: new FormControl(false),
    isRecurring: new FormControl(false)
  });

  logo: string = null;

  options: ResizeOptions = {
        resizeMaxHeight: 128,
        resizeMaxWidth: 128
  };

  latitude: number = 39.8282;
  longitude: number = -98.5795;
  zoom: number = 4;
  poster: any;
  viewState: ViewState;
  recurringDays: string[] = [];

  @ViewChild('agmMap') agmMap : AgmMap;

  constructor(private store: Store<AppState>, private router: Router, private ngZone: NgZone, private db: AngularFireDatabase) { }

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state;
    }).subscribe((state: AppState) => {
       this.poster = state.data.newPoster;
       this.viewState = state.view;
       if(this.viewState.service != 'CampusLive'){
         this.longitude = this.poster.longitude;
         this.latitude = this.poster.latitude;
       } else {
         if ("geolocation" in navigator) {
            navigator.geolocation.getCurrentPosition((position) => {
              this.latitude = position.coords.latitude;
              this.longitude = position.coords.longitude;
              this.zoom = 12;
            });
          }
       }
      });
  }

  selected(imageResult: ImageResult, dest:string): void {
    if (imageResult.error) alert(imageResult.error);
    this[dest] = imageResult.resized
      && imageResult.resized.dataURL
      || imageResult.dataURL;
    this.pushFileToStorage(imageResult, dest);
  }

  setAddress(place: any): void {
    this.ngZone.run(() => {
      this.latitude = place.geometry.location.lat();
      this.longitude = place.geometry.location.lng();
      this.zoom = 12;
    });
  }

  markerDragEnd(m: any, $event: any): void {
    this.latitude = $event.coords.lat;
    this.longitude = $event.coords.lng;
  }

  pushFileToStorage(imageResult: ImageResult, dest:string): void {
    const storageRef = firebase.storage().ref();
    const uuid = generateUUID();
    const uploadTask = storageRef.child(uuid + "_"  + imageResult.file.name).put(imageResult.file);
 
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (snapshot) => {
        const snap = snapshot as firebase.storage.UploadTaskSnapshot
      },
      (error) => {
        console.log(error)
      },
      () => {
        this[dest]=uploadTask.snapshot.downloadURL
      }
    );
  }

  submit(): void{
    if(this.model.valid) {
      let submissionModel = {
        logo_url: this.logo,
        longitude: this.longitude,
        latitude: this.latitude,
        isEntityOrg: false,
        name: this.model.value.name,
        type: this.model.value.type,
        website: this.model.value.website,
        description: this.model.value.description,
        start_date: (new Date(this.model.value.start_date)).toLocaleString([], {hour: '2-digit', minute:'2-digit', year: 'numeric', month: 'numeric', day: 'numeric'}).replace(/,/g, ''),
        end_date: (new Date(this.model.value.end_date)).toLocaleString([], {hour: '2-digit', minute:'2-digit', year: 'numeric', month: 'numeric', day: 'numeric'}).replace(/,/g, ''),
        location: this.poster.name,
        isFeatured: this.model.value.isFeatured,
        isRecurring: this.model.value.isRecurring,
        entity_id: this.poster.$key,
        recurring_days: this.model.value.isRecurring ? this.recurringDays : null
      };
      if(this.viewState.service == 'CampusLive'){
        submissionModel.isEntityOrg = true;
        let info = this.db.list('/school_posts/' + getCity(this.poster.school) + '/' + this.poster.school + '/');
        info.push(submissionModel);
        this.router.navigate(['/view/organization']);
      } else {
        let info = this.db.list('/city_posts/' + this.poster.city + '/');
        info.push(submissionModel);
        this.router.navigate(['/view/business']);
      }
    }
  }

  modifyRecurringDay(day: string){
    let idx = this.recurringDays.indexOf(day);
    if(idx == -1){
      this.recurringDays.push(day);
    } else {
      this.recurringDays.splice(idx, 1);
    }
  }

  logout(): void {
    this.store.dispatch({type: AppActions.LOGOUT, payload: null});
    this.router.navigate(['login']);
  }

  cancel(): void{
    if(this.viewState.service == 'CampusLive'){
        this.router.navigate(['/view/organization']);
      } else {
        this.router.navigate(['/view/business']);
      }
  }
}


