import { Component, ElementRef, NgModule, NgZone, OnInit, ViewChild } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState, ViewState } from '../../store/state';
import { AppActions } from '../../store/actions/appActions';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { ImageResult, ResizeOptions } from 'ng2-imageupload';
import { AgmCoreModule, MapsAPILoader } from '@agm/core';
import { AgmMap } from '@agm/core';
import * as firebase from 'firebase';
import { AngularFireDatabase } from 'angularfire2/database';
import { generateUUID, tConvert } from '../../shared/functions/functions';
import { Ng2ImgToolsService } from 'ng2-img-tools';

@Component({
  selector: 'app-newbusiness',
  templateUrl: './business.component.html',
  styleUrls: ['./business.component.scss']
})
export class NewBusinessComponent implements OnInit {

  model: FormGroup = new FormGroup({
    name: new FormControl('', Validators.required),
    type: new FormControl('', Validators.required),
    category: new FormControl('Experiences', Validators.required),
    ranking: new FormControl(1, Validators.required),
    mondayStart: new FormControl('', Validators.required),
    mondayEnd: new FormControl('', Validators.required),
    tuesdayStart: new FormControl('', Validators.required),
    tuesdayEnd: new FormControl('', Validators.required),
    wednesdayStart: new FormControl('', Validators.required),
    wednesdayEnd: new FormControl('', Validators.required),
    thursdayStart: new FormControl('', Validators.required),
    thursdayEnd: new FormControl('', Validators.required),
    fridayStart: new FormControl('', Validators.required),
    fridayEnd: new FormControl('', Validators.required),
    saturdayStart: new FormControl('', Validators.required),
    saturdayEnd: new FormControl('', Validators.required),
    sundayStart: new FormControl('', Validators.required),
    sundayEnd: new FormControl('', Validators.required),
    phone: new FormControl('', Validators.required),
    website: new FormControl('', Validators.required),
    city: new FormControl('San Diego', Validators.required),
    zipcode: new FormControl('', Validators.required),
    description: new FormControl('')
  });

  logo: string = null;
  logo_resized:string = null;
  img1: string = null;
  img2: string = null;
  img3: string = null;

  options: ResizeOptions = {
        resizeMaxHeight: 128,
        resizeMaxWidth: 128
  };

  latitude: number = 39.8282;
  longitude: number = -98.5795;
  zoom: number = 4;

  subroute: string;
  mask = ['(', /[1-9]/, /\d/, /\d/, ')', '-', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];

  @ViewChild('agmMap') agmMap : AgmMap;

  constructor(private ng2ImgToolsService: Ng2ImgToolsService, private store: Store<AppState>, private router: Router, private ngZone: NgZone, private db: AngularFireDatabase) { }

  ngOnInit():void {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.zoom = 12;
      });
    }
    this.store.select((state: AppState) => {
        return state.view;
    }).subscribe((view: ViewState) => {
      this.subroute = view.subroute;
    });
  }

  selected(imageResult: ImageResult, dest:string): void {
    if (imageResult.error) alert(imageResult.error);
    this[dest] = imageResult.resized
      && imageResult.resized.dataURL
      || imageResult.dataURL;
    this.pushFileToStorage(imageResult, dest);
    this.ng2ImgToolsService.resize([imageResult.file], 50, 50).subscribe(result => {
        this.pushFileToStorage(result, 'logo_resized');
    }, error => {
        console.log(error);
    });
  }

  setAddress(place: any): void {
    this.ngZone.run(() => {
      this.latitude = place.geometry.location.lat();
      this.longitude = place.geometry.location.lng();
      this.zoom = 12;
    });
  }

  markerDragEnd(m: any, $event: any): void {
    this.latitude = $event.coords.lat;
    this.longitude = $event.coords.lng;
  }

  pushFileToStorage(imageResult: ImageResult, dest:string): void {
    const storageRef = firebase.storage().ref();
    const uuid = generateUUID();
    const uploadTask = storageRef.child(uuid + "_"  + imageResult.file.name).put(imageResult.file);
 
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (snapshot) => {
        const snap = snapshot as firebase.storage.UploadTaskSnapshot
      },
      (error) => {
        console.log(error)
      },
      () => {
        this[dest]=uploadTask.snapshot.downloadURL
      }
    );
  }

  submit(): void{
    if(this.model.valid && this.logo){
      let submissionModel = {
        businessHours: {
          monday: tConvert(this.model.value.mondayStart) + ' - ' + tConvert(this.model.value.mondayEnd),
          tuesday: tConvert(this.model.value.tuesdayStart) + ' - ' + tConvert(this.model.value.tuesdayEnd),
          wednesday: tConvert(this.model.value.wednesdayStart) + ' - ' + tConvert(this.model.value.wednesdayEnd),
          thursday: tConvert(this.model.value.thursdayStart) + ' - ' + tConvert(this.model.value.thursdayEnd),
          friday: tConvert(this.model.value.fridayStart) + ' - ' + tConvert(this.model.value.fridayEnd),
          saturday: tConvert(this.model.value.satudayStart) + ' - ' + tConvert(this.model.value.saturdayEnd),
          sunday: tConvert(this.model.value.sundayStart) + ' - ' + tConvert(this.model.value.sundayEnd),
        },
        logo_url: this.logo || null,
        longitude: this.longitude,
        latitude: this.latitude,
        isOrg: false,
        image1_url: this.img1 || null,
        image2_url: this.img2 || null,
        image3_url: this.img3 || null,
        name: this.model.value.name,
        type: this.model.value.type,
        category: this.model.value.category,
        ranking: this.model.value.ranking,
        phone: this.model.value.phone,
        website: this.model.value.website,
        zipcode: this.model.value.zipcode,
        description: this.model.value.description
      };
      let info = this.db.list('/city_entities_info/' + this.model.value.city + '/');
      info.push(submissionModel).then((item) => {
        let clientModel = {
          businessHours: {
            monday: tConvert(this.model.value.mondayStart) + ' - ' + tConvert(this.model.value.mondayEnd),
            tuesday: tConvert(this.model.value.tuesdayStart) + ' - ' + tConvert(this.model.value.tuesdayEnd),
            wednesday: tConvert(this.model.value.wednesdayStart) + ' - ' + tConvert(this.model.value.wednesdayEnd),
            thursday: tConvert(this.model.value.thursdayStart) + ' - ' + tConvert(this.model.value.thursdayEnd),
            friday: tConvert(this.model.value.fridayStart) + ' - ' + tConvert(this.model.value.fridayEnd),
            saturday: tConvert(this.model.value.satudayStart) + ' - ' + tConvert(this.model.value.saturdayEnd),
            sunday: tConvert(this.model.value.sundayStart) + ' - ' + tConvert(this.model.value.sundayEnd),
          },
          category: this.model.value.category,
          isOrg: false,
          latitude: this.latitude,
          logo_url: this.logo_resized || null,
          longitude: this.longitude,
          name: this.model.value.name,
          ranking: this.model.value.ranking,
          type: this.model.value.type
        };
        let client = this.db.list('/city_entities/' + this.model.value.city + '/');
        client.update(item.key, clientModel);
      });
      this.router.navigate(['/admin/' + this.subroute]);
      
    }
  }

  logout(): void {
    this.store.dispatch({type: AppActions.LOGOUT, payload: null});
    this.router.navigate(['login']);
  }

  cancel(): void{
    this.router.navigate(['/admin/' + this.subroute]);
  }
}


