import { Component, ElementRef, NgModule, NgZone, OnInit, ViewChild } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState, ViewState } from '../../store/state';
import { AppActions } from '../../store/actions/appActions';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { ImageResult, ResizeOptions } from 'ng2-imageupload';
import * as firebase from 'firebase';
import { AngularFireDatabase } from 'angularfire2/database';
import { Ng2ImgToolsService } from 'ng2-img-tools';
import { generateUUID, getCity } from '../../shared/functions/functions';

@Component({
  selector: 'app-neworganization',
  templateUrl: './organization.component.html',
  styleUrls: ['./organization.component.scss']
})
export class NewOrganizationComponent implements OnInit {

  model: FormGroup = new FormGroup({
    name: new FormControl('', Validators.required),
    type: new FormControl('', Validators.required),
    category: new FormControl('Campus Affiliated', Validators.required),
    ranking: new FormControl(1, Validators.required),
    email: new FormControl('', Validators.email),
    phone: new FormControl(''),
    website: new FormControl('', Validators.required),
    city: new FormControl('UC San Diego', Validators.required),
    zipcode: new FormControl('', Validators.required),
    description: new FormControl('')
  });

  logo_resized:string = null;
  logo: string = null;
  img1: string = null;
  img2: string = null;
  img3: string = null;
  mask = ['(', /[1-9]/, /\d/, /\d/, ')', '-', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];

  options: ResizeOptions = {
        resizeMaxHeight: 128,
        resizeMaxWidth: 128
  };

  subroute: string;

  constructor(private ng2ImgToolsService: Ng2ImgToolsService, private store: Store<AppState>, private router: Router, private db: AngularFireDatabase) { }

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state.view;
    }).subscribe((view: ViewState) => {
      this.subroute = view.subroute;
    });
  }

  selected(imageResult: ImageResult, dest:string): void {
    if (imageResult.error) alert(imageResult.error);
    this[dest] = imageResult.resized
      && imageResult.resized.dataURL
      || imageResult.dataURL;
    this.pushFileToStorage(imageResult, dest);
    this.ng2ImgToolsService.resize([imageResult.file], 50, 50).subscribe(result => {
        this.pushFileToStorage(result, 'logo_resized');
    }, error => {
        console.log(error);
    });
  }

  pushFileToStorage(imageResult: ImageResult, dest:string): void {
    const storageRef = firebase.storage().ref();
    const uuid = generateUUID();
    const uploadTask = storageRef.child(uuid + "_"  + imageResult.file.name).put(imageResult.file);
 
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (snapshot) => {
        const snap = snapshot as firebase.storage.UploadTaskSnapshot
      },
      (error) => {
        console.log(error)
      },
      () => {
        this[dest]=uploadTask.snapshot.downloadURL
      }
    );
  }

  submit(): void{
    if(this.model.valid && this.logo){
      let submissionModel = {
        logo_url: this.logo,
        isOrg: true,
        image1_url: this.img1,
        image2_url: this.img2,
        image3_url: this.img3,
        name: this.model.value.name,
        type: this.model.value.type,
        category: this.model.value.category,
        ranking: this.model.value.ranking,
        email: this.model.value.email,
        phone: this.model.value.phone,
        website: this.model.value.website,
        zipcode: this.model.value.zipcode,
        description: this.model.value.description
      };
      let info = this.db.list('/school_entities_info/' + getCity(this.model.value.city) + '/' + this.model.value.city + '/');
      info.push(submissionModel).then((item) => {
        let clientModel = {
          category: this.model.value.category,
          isOrg: true,
          logo_url: this.logo_resized,
          name: this.model.value.name,
          ranking: this.model.value.ranking,
          type: this.model.value.type
        };
        let client = this.db.list('/school_entities/' + getCity(this.model.value.city) + '/' + this.model.value.city + '/');
        client.update(item.key, clientModel);
      });
      this.router.navigate(['/admin/' + this.subroute]);
    }
  }

  logout(): void {
    this.store.dispatch({type: AppActions.LOGOUT, payload: null});
    this.router.navigate(['login']);
  }

  cancel(): void{
    this.router.navigate(['/admin/' + this.subroute]);
  }
}


