import { Route } from '@angular/router';
import { NewComponent } from './new.component';
import { NewSightComponent } from './sight/sight.component';
import { NewBusinessComponent } from './business/business.component';
import { NewOrganizationComponent } from './organization/organization.component';
import { NewPostComponent } from './post/post.component';

export const NewRoutes: Route[] = [
  {
    path: '',
    component: NewComponent,
    children: [
      {
        path: 'post',
        component: NewPostComponent
      },
      {
        path: 'organization',
        component: NewOrganizationComponent
      },
      {
        path: 'business',
        component: NewBusinessComponent
      },
      {
        path: 'sight',
        component: NewSightComponent
      }
    ]
  }
];


